.navbar {
  display: flex;
  position: relative;
  justify-content: space-between;
  align-items: center;
  background-color: #333;
  color: white;
  height: 52px;
  -webkit-box-shadow: 0px 2px 5px 0px rgba(50, 50, 50, 0.81);
  -moz-box-shadow: 0px 2px 5px 0px rgba(50, 50, 50, 0.81);
  box-shadow: 0px 2px 5px 0px rgba(50, 50, 50, 0.81);
}

.brand-title {
  font-size: 1.5rem;
  margin: 0.5rem;
  text-decoration: none;
}

.navbar-links {
  height: 100%;
}

.navbar-links ul {
  display: flex;
  margin: 0;
  padding: 0;
}

.navbar-links li {
  list-style: none;
}

.navbar-links li a {
  display: block;
  text-decoration: none;
  color: white;
  padding: 1rem;
}

.navbar-links li:hover {
  background-color: #555;
}

.toggle-button {
  position: absolute;
  top: 0.75rem;
  right: 1rem;
  display: none;
  flex-direction: column;
  justify-content: space-between;
  width: 30px;
  height: 21px;
}

.toggle-button .bar {
  height: 3px;
  width: 100%;
  background-color: white;
  border-radius: 10px;
}

@media (max-width: 800px) {
  .navbar {
    flex-direction: column;
    align-items: flex-start;
  }

  .toggle-button {
    display: flex;
  }

  .navbar-links {
    // display: none;
    width: 100%;
  }

  .navbar-links ul {
    width: 100%;
    flex-direction: row;
  }

  .navbar-links ul li {
    text-align: center;
  }

  .navbar-links ul li a {
    padding: 0.5rem 0.5rem;
    font-size: small;
  }

  .navbar-links.active {
    display: flex;
  }
}

.brand Link {
  text-decoration: none;
  text-decoration-style: none;
  color: white;
}
